/**
 *  Default settings
 */

.main-content.active .navigation {
  left: 0;
}
.control {
  max-width: 400px;
  margin: auto;
  width: 100%;
}
.mobile-menu {
  width: 40px;
  height: 45px;
  position: fixed;
  right: 10px;
  top: 50px;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
}
.mobile-menu span {
    display: block;
    position: absolute;
    height: 9px;
    width: 100%;
    background: #ffffff;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
}
.mobile-menu span:nth-child(1) {
      top: 0px;
}
.mobile-menu span:nth-child(2), .mobile-menu span:nth-child(3) {
      top: 18px;
}
.mobile-menu span:nth-child(4) {
      top: 36px;
}
.mobile-menu.active span:nth-child(1) {
    top: 18px;
    width: 0%;
    left: 50%;
}
.mobile-menu.active span:nth-child(2) {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.mobile-menu.active span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.mobile-menu.active span:nth-child(4) {
    top: 18px;
    width: 0%;
    left: 50%;
}
.navigation {
  z-index: 1;
  padding-top: 30px;
  -webkit-transition: 300ms;
  transition: 300ms;
  position: fixed;
  width: 320px;
  left: -320px;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
}
.navigation a {
    display: block;
    padding: 10px 5px;
    text-decoration: none;
    margin: 5px;
    border-radius: 5px;
    background-color: #355CA9;
    color: #ffffff;
    outline: 0;
}
form {
  margin: auto;
}
form input {
    width: 100% !important;
